import React,{useEffect} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {listarProductosAction} from '../actions/productoActions';
import Producto from './Producto';

const Productos = () => {
    const dispatch=useDispatch();
    const productosInit=useSelector(store=>store.productos.productos);

    useEffect(()=>{
        const descargarProductos=()=>dispatch(listarProductosAction(productosInit));
        descargarProductos();
    }, []);
    const {productos, loading}=useSelector(state=>state.productos);
    return (
        <>
            <h2 className="text-center my-5">Listado Productos</h2>

            <table className="table table--striped">
                <thead className="bg-primary table-dark">
                    <tr>
                        <th scope="col">Nombre</th>
                        <th scope="col">Precio</th>
                        <th scope="col">Acciones</th>
                    </tr>
                </thead>
                <tbody>
                {productos.length===0?<tr><td>No hay productos</td></tr>:(
                    productos.map(producto=>(
                        <Producto
                            key={producto.id}
                            producto={producto}
                        />
                    ))
                )}

                {loading?<tr><td>Cargando...</td></tr>:null}
                </tbody>
            </table>
        </>
    )
}

export default Productos
